// 店铺信息相关接口
import axios from './../fetch'

// 获取店铺设置信息
export const getStoreSet = params => { return axios.get('/admin/store/storeSet/getStoreSet', { params: params }) }
// 判断店铺是否设置了信息
export const judgeStoreSet = params => { return axios.get('/admin/store/storeSet/judgeStoreSet', { params: params }) }
// 修改或新增店铺设置
export const updateStoreSet = params => { return axios.post('/admin/store/storeSet/updateStoreSet', params) }
// 获取店铺等级信息列表
export const getLevelList = params => { return axios.get('/admin/store/storeSet/getStoreLevelList', { params: params }) }
// 显示单个店铺结算账户
export const getSettleAccount = params => { return axios.get('/admin/store/settlementAccount/getAccount', { params: params }) }
// 判断是否设置了商家端的支付密码
export const getAccountSet = params => { return axios.get('/admin/store/storeAccount/getAccount', { params: params }) }

// 获取店铺结算账户集合
export const getAccountList = params => { return axios.get('/admin/store/settlementAccount/getAccountList', { params: params }) }
// 获取单个店铺结算账户(需传id)
export const getAccountOne = params => { return axios.get('/admin/store/settlementAccount/getStoreAccount', { params: params }) }
// 添加店铺结算账户
export const saveSettleAccount = params => { return axios.post('/admin/store/settlementAccount/saveAccount', params) }
// 修改店铺结算账户
export const updateSettleAccount = params => { return axios.post('/admin/store/settlementAccount/updateAccount', params) }
// 删除店铺结算账户
export const deleteSettleAccount = params => { return axios.post('/admin/store/settlementAccount/deleteAccount', params) }
// 判断店铺结算账户是否达5个
export const settleNumber = params => { return axios.get('/admin/store/settlementAccount/getAccountNumber', { params: params }) }
// 修改支付密码时，邮箱和手机号的显示信息
export const getInformation = params => { return axios.get('/admin/store/storeAccount/getInformation', { params: params }) }
// 发送手机验证码
export const sendPhone = params => { return axios.get('/admin/store/storeAccount/sendPhone', { params: params }) }
// 发送邮箱验证码
export const sendEmail = params => { return axios.get('/admin/store/storeAccount/sendEmail', { params: params }) }
// 验证手机验证码
export const verifyPhone = params => { return axios.post('/admin/store/storeAccount/verificationPhoneMsg', params) }
// 验证邮箱验证码
export const verifyEmail = params => { return axios.post('/admin/store/storeAccount/verificationEmailMsg', params) }
// 修改、设置支付密码
export const savePayPwd = params => { return axios.post('/admin/store/storeAccount/updatePayPwd', params) }
// 补齐主营类目保证金操作提示
export const storeSetOperationHint = params => { return axios.get('/admin/store/storeSet/operationHint', { params: params }) }
// 补齐主营类目保证金差额
export const storeSetSupplement = params => { return axios.post('/admin/store/storeSet/supplement', params) }



