<template>
  <div>
     <cms-prompt collapse='true'>
      <li>您已保存{{tableData.length}}个结算账户，还能保存{{5-tableData.length}}个结算账户</li>
      <li>与平台的周期性结算时，系统默认将金额结算至默认账户中，避免资金安全问题，建议不要随意设置默认结算账户</li>
    </cms-prompt>
    <el-form 
     :model="formData" label-width="210px" :rules="rules" ref="ruleForm" v-loading="loading">
      <el-form-item label="收款类型：" prop="">
         <el-radio v-model="formData.accountType" :label="1">支付宝</el-radio>
      </el-form-item>
      <el-form-item label="收款账户：" prop="account">
          <el-input v-model="formData.account" maxlength="30"></el-input>
      </el-form-item>
      <el-form-item label="账户姓名：" prop="accountName">
         <el-input v-model="formData.accountName" maxlength="12"></el-input>
      </el-form-item>
       <el-form-item label="">
           <el-checkbox v-model="formData.isDefault">设为默认</el-checkbox>
      </el-form-item>
       <el-form-item label="" >
        <el-button type="primary" size="mini" style="width:80px;" @click="saveAccount"
          v-if="canSaveAccount"> 提交
        </el-button>
      </el-form-item>
    </el-form>
    <div class="account-wrap" >
      <div class="account-item" v-for="(item,index) in tableData" :key="index">
        <span class="account-badge" v-if="item.isDefault">默认</span>
        <p><span class="safe-title">账户类型：</span><span class="safe-value">{{item.accountType|accountTypeFilter}}</span></p>
        <p><span class="safe-title">账户：</span><span class="safe-value">{{item.account}}</span></p>
        <div class="account-audit">
          <p><span class="safe-title">姓名：</span><span class="safe-value">{{item.accountName}}</span></p>
          <p>
            <i class="el-icon-edit" style="margin-right:5px;" @click="updateAccount(item.id)"></i>
            <i class="el-icon-close" @click="removeAccount(item.id)" v-if="!item.isDefault"></i>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAccountList, saveSettleAccount, deleteSettleAccount, settleNumber,
  updateSettleAccount, getAccountOne } from '@/api/shop/shopInfo.js'
import rules from '@/rules'
var defaultData = {
  'accountType': 1,
  'account': '',
  'accountName': '',
  'isDefault': false
}
export default {
  data() {
    var validateAccount = (rule, value, callback) => {
      const reg = /![\u4e00-\u9fa5]+/
      const reg1 = /^[?=\x21-\x7eA-Za-z0-9]+[?=\x21-\x7eA-Za-z0-9\s]*[?=\x21-\x7eA-Za-z0-9]+$/
      if (value === '') {
        callback(new Error('请输入收款账户'))
      } else if (!reg.test(value) && !reg1.test(value)) {
        callback(new Error('账户格式不正确'))
      } else {
        callback()
      }
    }
    var validateName = (rule, value, callback) => {
      const reg = /^[\u4E00-\u9FA5A-Za-z]+$/
      if (value === '') {
        callback(new Error('请输入账户姓名'))
      } else if (!reg.test(value)) {
        callback(new Error('账户姓名格式不正确'))
      } else {
        callback()
      }
    }
    return {
      tableData: '',
      formData: Object.assign({}, defaultData), // 提交修改的列表数据
      loading: false,
      canSaveAccount: '', // 提交按钮的显示隐藏
      isUpdate: false, // 判断是新增还是修改操作
      rules: {
        account: [
          rules.custom(validateAccount),
          rules.required()
        ],

        accountName: [
          rules.custom(validateName),
          rules.required()
        ]
      }
    }
  },
  filters: {
    accountTypeFilter(type) {
      const typeArr = ['', '支付宝 ', '微信', '银联']
      return typeArr[parseInt(type)]
    }
  },
  methods: {
    getTableData() { // 获取店铺结算账户集合
      this.loading = true
      getAccountList().then(res => {
        if (res.code === 200) {
          this.tableData = res.data
          this.loading = false
          settleNumber().then(res => { // 判断账户是否达到上限
            if (res.code === 200) {
              this.canSaveAccount = res.data// 控制新增按钮
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    saveAccount() { // 提交
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          if (!this.isUpdate) { // 新增操作
            saveSettleAccount(this.formData).then(res => {
              if (res.code === 200) {
                this.$successMessage('添加成功')
                this.getTableData()
                this.formData = Object.assign({}, defaultData) // 清空表单数据
              } else {
                this.$errorMessage(res.message)
              }
            })
          } else {
            updateSettleAccount(this.formData).then(res => { // 修改操作
              if (res.code === 200) {
                this.$successMessage('修改成功')
                this.canSaveAccount = false// 需要隐藏按钮
                this.formData = Object.assign({}, defaultData) // 清空表单数据
                this.getTableData()// 重新获取表单数据
                this.isUpdate = false// 把按钮事件设置为新增操作
              } else {
                this.$errorMessage(res.message)
              }
            })
          }
        } else {
          return false
        }
      })
    },
    updateAccount(id) { // 修改按钮事件
      getAccountOne({ id: id }).then(res => { // 回填账户信息
        if (res.code === 200) {
          this.formData = res.data
          this.canSaveAccount = true// 先显示按钮
          this.isUpdate = true// 把按钮设置为提交操作
          this.$refs['ruleForm'].clearValidate()// 清空表单验证
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    removeAccount(id) { // 删除账户
      this.$confirm('确定删除该条账户信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteSettleAccount({ id: id }).then(res => {
          if (res.code === 200) {
            this.$successMessage('删除成功')
            this.getTableData()
            this.tableData.forEach(v => { // 删除成功之后查找该条数据是否被回填过
              if (v.id === id) {
                this.formData = Object.assign({}, defaultData) // 清空表单数据
              }
            })
          } else {
            this.$errorMessage(res.message)
          }
        })
      })
    }
  },
  created() {
    this.getTableData()
  }
}
</script>

<style scoped>
.account-wrap{
  margin: 20px 10% 0;
  display: flex;
  flex-wrap: wrap;
}
.account-item{
  border: 1px solid #188AE2;
  width: 22%;
  padding: 10px;
  box-shadow: 1px 1px 2px 2px #e0e0e0;
  position: relative;
  margin: 0 0 20px 20px; 
  border-radius: 5px;
}
.safe-title{
  display: inline-block;
  width: 70px;
  text-align: right;
}
.safe-value{
  display: inline-block;
  max-width: 120px;
}
.account-audit{
  display: flex;
  justify-content: space-between;
}
.account-audit .el-icon-edit,.account-audit .el-icon-close{
  cursor: pointer;
}
.account-badge{
  display: inline-block;
  position: absolute;
  top:6px;
  right: 6px;
  width: 30px;
  height: 18px;
  color: #fff;
  background-color: #188AE2;
  font-size: 12px;
  border-radius: 8px;
  text-align: center;
  line-height: 18px;
}
</style>



