 <template>
  <div>
    <component :is="current" @event="skip"></component>
  </div>
</template>

<script>
import List from './safeSet/list'
import Admin from './safeSet/admin'
import Password from './safeSet/password'
export default {
  components: {
    List,
    Admin,
    Password
  },
  data() {
    return {
      current: 'List'
    }
  },
  methods: {
    skip(data) { // 接受子组件传过来的值
      this.current = data
    },
    backUpper() { // 供父组件调用返回列表的方法
      this.current = 'List'
    }
  }
}
</script>
<style scoped>

</style>




