<template>
  <div v-loading="loading">
    <ul class="step-wrap">
      <li class="step-item" :style="{'color':process>=1?color:'','border-color':process>=1?color:''}">①验证身份</li>
      <li class="step-item" :style="{'color':process>=2?color:'','border-color':process>=2?color:''}">②设置支付密码</li>
      <li class="step-item" :style="{'color':process>=3?color:'','border-color':process>=3?color:''}">③完成</li>
    </ul>
    <div class="update-info-container">
     <el-form ref="verifyData" :model="verifyData" label-width="210px" v-if="process==1"
       width="100%" :rules="rulesVerify" :hide-required-asterisk=true>
      <el-form-item label="验证方式：" >
        <el-select v-model="verifyMode" placeholder="请选择" style="width:80px;" @change="changeVerifyMode">
         <el-option value="1" label="邮箱"></el-option>
         <el-option value="2" label="手机"></el-option>
        </el-select>
        <el-input style="width:185px;" v-model="storePhone" disabled></el-input>
        <div v-if="!loading">
          <p class="mode-disabled" v-show="verifyMode=='1'&&!storePhone">邮箱验证方式未进行设置，无法使用，请更换验证方式</p>
          <p class="mode-disabled" v-show="!storePhone&&verifyMode=='2'">手机验证方式未进行设置，无法使用，请更换验证方式</p>
        </div>
      </el-form-item>
      <el-form-item label="验证码：" prop="verifyCode" class="next">
        <el-input v-model="verifyData.verifyCode" style="width:180px;" class="code-input"></el-input>
        <el-button type="primary" size="mini" class="send-code" @click="sendCode" v-show="mailOrPhone.show"
         :disabled="disabledSendMail" ref="oMail">发送验证码</el-button>
        <el-button type="primary" size="mini" class="send-code" @click="sendCode" v-show="!mailOrPhone.show"
         :disabled="disabledSendPhone" ref="oPhone">发送验证码</el-button>
      </el-form-item>
      <el-form-item style="padding-top:10px;">
        <el-button type="primary" size="mini" style="width:80px;" @click="confirmCode" :disabled="disabledStep">
          下一步
        </el-button>
      </el-form-item>
     </el-form>
     <!-- 第二步的表单 -->
     <el-form ref="updateData" :model="updateData" label-width="210px" v-if="process==2" :rules="rules">
      <el-form-item label="新支付密码：" prop="payPassword" class="pwd-new">
        <el-input type="password" style="width:200px;" v-model="updateData.payPassword" 
         minlength="6" maxlength="16"></el-input>
        <div class="gray">请输入6~16位的数字与字母组合</div>
      </el-form-item>
      <el-form-item label="确认支付密码：" prop="checkPassword">
        <el-input type="password" style="width:200px;"  v-model="updateData.checkPassword" minlength="6" maxlength="16"
         ></el-input>
      </el-form-item>
      <el-form-item label="验证码：" prop="code">
       <el-input style="width:100px;" v-model="updateData.code"></el-input>
        <img :src="captchaImg"  @click="getCaptcha" class="cap_image" width="100">
      </el-form-item>
      <el-form-item label="" prop="parentId">
        <el-button type="primary" size="mini" style="width:80px;" @click="updatePassword">提交</el-button>
      </el-form-item>
     </el-form>
     <!-- 第三步的样式 -->
     <div v-if="process==3" style="display:flex;align-items:center;">
       <div>
         <i class="el-icon-circle-check" style="color:#2ED540;font-size:25px;margin-right:20px;"></i>
       </div>
       <div style="font-size:14px;">
         <p style="color:#2ED540;">支付密码修改成功！</p>
         <p>请牢记支付密码
           <span class="back-setting" @click="$emit('event', 'List')" :style="{'color':color}">返回安全设置</span>
          </p>
       </div>
     </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import cms from '@/utils/index'
import { getInformation, sendPhone, sendEmail, verifyPhone, verifyEmail, savePayPwd } from '@/api/shop/shopInfo.js'
import { getCaptcha } from '@/api/global.js'
var postData = {
  'payPassword': '',
  'checkPassword': '',
  'code': '',
  'token': '',
  'sessionId': ''
}
export default {
  data() {
    var validateCheckPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.updateData.payPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      verifyMode: '1', // 第一步验证的方式
      storePhone: '', // 第一步用户手机号及邮箱号显示
      verifyData: { // 第一步用户输入的验证码
        verifyCode: ''
      },
      tableData: '', // 用户绑定的邮箱和手机号信息
      disabledSendMail: false, // 发送验证码按钮的只读
      disabledSendPhone: false,
      mailOrPhone: { show: true }, // 发送手机或邮箱验证码之间的切换
      disabledStep: true, // 下一步按钮的禁用控制
      updateData: Object.assign({}, postData), // 第二步新增或修改的表单信息
      captchaImg: '', // 第二步图片验证码的地址信息
      process: 1, // 各阶段的内容及样式的控制
      loading: false,
      timerMail: null, // 定时器
      timerPhone: null, // 定时器
      rulesVerify: {
        verifyCode: [
          rules.required('请输入验证码')
        ]
      },
      rules: {
        payPassword: [
          rules.required('请输入密码', 'blur')
        ],
        checkPassword: [
          rules.custom(validateCheckPass),
          rules.min(6),
          rules.required('请再次输入密码')
        ],
        code: [
          rules.required('请输入验证码', 'blur'),
          rules.$length(4)
        ]
      }
    }
  },
  watch: {
    verifyData: {
      handler(newV, oldV) {
        if (newV.verifyCode !== '') {
          this.disabledStep = false
        } else {
          this.disabledStep = true
        }
      },
      deep: true
    }
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    getTableData() { // 获取绑定的邮箱和手机号
      this.loading = true
      getInformation().then(res => {
        if (res.code === 200) {
          this.tableData = res.data
          this.storePhone = res.data.emailData
          this.loading = false
          if (!res.data.emailData) { // 若没有绑定邮箱则禁用发送验证码按钮
            this.disabledSend = true
          }
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    updatePassword() { // 设置修改支付密码的提交按钮
      this.$refs['updateData'].validate((valid) => {
        if (valid) {
          this.updateData.payPassword = cms.encrypt(this.updateData.payPassword)// 加密
          delete this.updateData.checkPassword// 确认密码不需要提交
          savePayPwd(this.updateData).then(res => {
            if (res.code === 200) {
              this.$successMessage('设置成功')
              this.process = 3
            } else {
              this.$errorMessage(res.message)
              this.getCaptcha()// 失败重新获取验证图片
              this.updateData.code = ''
            }
          })
        } else {
          return false
        }
      })
    },
    confirmCode() { // 一阶段下一步按钮操作
      this.$refs['verifyData'].validate((valid) => {
        if (valid) {
          if (this.verifyMode === '1') { // 选择邮箱则验证邮箱
            verifyEmail({ 'code': this.verifyData.verifyCode }).then(res => {
              if (res.code === 200) {
                this.updateData.token = res.data// 赋值token
                this.process = 2
                clearInterval(this.timerMail)
                this.timerMail = null
              } else {
                this.$errorMessage(res.message)
              }
            })
          } else { // 验证手机
            verifyPhone({ 'code': this.verifyData.verifyCode }).then(res => {
              if (res.code === 200) {
                this.process = 2
                this.updateData.token = res.data// 赋值token
                clearInterval(this.timerPhone)// 清空定时器
                this.timerPhone = null
              } else {
                this.$errorMessage(res.message)
              }
            })
          }
        } else {
          return false
        }
      })
    },
    countdownMail() { // 发送验证码的倒计时
      let target = 120
      const that = this
      that.timerMail = setInterval(function() {
        target--
        that.disabledSendMail = true
        that.$refs['oMail'].$el.innerText = target + '秒后重试'
        if (target <= 0) {
          clearInterval(that.timerMail)
          that.timerMail = null
          that.disabledSendMail = false// 取消禁用发送验证码按钮
          that.$refs['oMail'].$el.innerText = '发送验证码'
        }
      }, 1000)
    },
    countdownPhone() { // 发送验证码的倒计时
      let target = 120
      const that = this
      that.timerPhone = setInterval(function() {
        target--
        that.disabledSendPhone = true
        that.$refs['oPhone'].$el.innerText = target + '秒后重试'
        if (target <= 0) {
          clearInterval(that.timerPhone)
          that.timerPhone = null
          that.disabledSendPhone = false// 取消禁用发送验证码按钮
          that.$refs['oPhone'].$el.innerText = '发送验证码'
        }
      }, 1000)
    },
    sendCode() { // 发送验证码
      if (this.verifyMode === '1') { // 方式1 发送邮箱验证码
        sendEmail().then(res => {
          if (res.code === 200) {
            this.countdownMail()
          } else if (res.code === 10514) { // 重复发送返回的状态码
            this.$errorMessage(res.message)
            this.disabledSendMail = true// 禁用发送验证码按钮
          } else {
            this.$errorMessage(res.message)
          }
        })
      } else {
        sendPhone().then(res => { // 方式2  发送手机验证码
          if (res.code === 200) {
            this.countdownPhone()
          } else if (res.code === 10514) {
            this.$errorMessage(res.message)
            this.disabledSendPhone = true// 禁用发送验证码按钮
          } else {
            this.$errorMessage(res.message)
          }
        })
      }
    },
    changeVerifyMode(val) { // 选择验证方式的下拉框
      this.$set(this.mailOrPhone, 'show', !this.mailOrPhone.show)
      if (val === '1') {
        this.storePhone = this.tableData.emailData
        if (!this.tableData.emailData) { // 若没有绑定邮箱则禁用发送验证码按钮
          this.disabledSendMail = true
        }
      } else {
        this.storePhone = this.tableData.phoneData
        if (!this.tableData.phoneData) { // 若没有绑定手机号码则禁用发送验证码按钮
          this.disabledSendPhone = true
        }
      }
    },
    getCaptcha() { // 获取图片验证码
      getCaptcha().then(res => {
        this.captchaImg = 'data:image/jpeg;base64,' + res.img
        this.updateData.sessionId = res.sessionId
      })
    }
  },
  created() {
    this.getTableData()// 获取绑定的邮箱和手机号
    this.getCaptcha() // 获取图片验证码
  },
  beforeDestroy() {
    clearInterval(this.timerMail)
    this.timerMail = null
    clearInterval(this.timerPhone)
    this.timerPhone = null
  }
}

</script>

<style scoped>
.step-wrap{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.step-item{
  width: 20%;
  font-size: 16px;
  border-bottom: 3px solid #e0e0e0;
  text-align: center;
  margin-right: 2px;
  border-radius: 2px;
}
.update-info-container{
  display: flex;
  justify-content: center;  
  align-items: center;
  margin-top: 70px;
}
.update-info-container .el-input{
  width: 200px;
}
.send-code{
  width: 90px;
  height: 28px;
  border-top: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: -4px;
}
.back-setting{
  cursor: pointer;
  margin-left: 5px;
}
.mode-disabled{
  font-size: 12px;
  color: red;
}
.cap_image{
 vertical-align:bottom;
 margin-left:-5px;

}
</style>



