<template>
<div class="cms-list-layout" style="padding-top:0;">
   <el-tabs v-model="activeName" @tab-click="handleClick" >
    <el-tab-pane label="店铺设置" name="first" >
     <shop-set></shop-set>
    </el-tab-pane>
    <el-tab-pane label="安全设置" name="second" lazy >
     <safe-list ref="safeList"></safe-list>
    </el-tab-pane>
  </el-tabs>
</div>
 
</template>

<script>
import shopSet from './shopSet'
import safeList from './safeList'
export default {
  name: 'shopInfoLayOut',
  components: {
    shopSet,
    safeList
  },
  data() {
    return {
      activeName: 'first'
    }
  },
  methods: {
    handleClick(tab) { // 跳转到安全设置
      if (tab.index === '1') {
        this.$nextTick(() => {
          this.$refs['safeList'].backUpper()
        })
      }
    }
  }
}
</script>

<style scoped>
.shop-contanier .el-tabs .el-tabs__header{
  margin: 0 ;
}
</style>



