<template>
  <div>
    <el-form
      ref="formName"
      :model="tableData"
      label-width="210px"
      class="table-wrap"
      v-loading="loading"
    >
      <el-form-item label="店铺名称：">
        <span v-if="tableData.store">{{tableData.store.storeName}}</span>
      </el-form-item>
      <el-form-item label="主营类目：" v-if="tableData.store.productCategory">
        <span>{{tableData.store.productCategory.cateName}}</span>
      </el-form-item>
      <el-form-item label="店铺状态：">
        <span>{{tableData.store.status==1?'营业中':(tableData.store.status==2?'暂停营业':'禁止营业')}}</span>
        <span
          v-if="!tableData.bondDissatisfaction&&!tableData.store.selfShop"
          style="color:#188AE2;cursor:pointer;margin-left:10px;"
          @click="make"
        >补交主营类目保证金</span>
      </el-form-item>
      <el-form-item label="店铺等级：">
        <div>
          <img
            :src="$cms.filterUrl(tableData.store.storeLevel.resourcesSpaceData.url)"
            width="20"
            height="20"
          >
          <span style="vertical-align: super;">{{tableData.store.storeLevel.levelName}}</span>
          <el-popover placement="right" width="630" trigger="click">
            <!-- 等级列表信息 -->
            <div style="padding:15px;">
              <p>店铺等级体系</p>
              <el-table :data="levelList" style="margin:10px 0">
                <el-table-column width="150" property="date" label="等级名称" align="center">
                  <template slot-scope="scope">
                    <img
                      :src="$cms.filterUrl(scope.row.url)"
                      style="width:20px;height:20px;vertical-align:middle;"
                    >
                    <span>{{scope.row.name}}</span>
                  </template>
                </el-table-column>
                <el-table-column width="150" property="name" label="成长值范围" align="center">
                  <template slot-scope="scope">
                    <span
                      v-if="Number(scope.row.maxExp)>=0"
                    >{{scope.row.minExp}} - {{scope.row.maxExp}}</span>
                    <span v-else>{{scope.row.maxExp}}</span>
                  </template>
                </el-table-column>
                <el-table-column width="150" property="releaseMaxNum" label="商品发布数量" align="center">
                  <template
                    slot-scope="scope"
                  >{{scope.row.releaseMaxNum===0?'无上限':scope.row.releaseMaxNum}}</template>
                </el-table-column>
                <el-table-column width="148" property="uploadMaxNum" label="资源空间使用量" align="center">
                  <template
                    slot-scope="scope"
                  >{{scope.row.uploadMaxNum===0?'无上限':scope.row.uploadMaxNum}}</template>
                </el-table-column>
              </el-table>
              <p>等级提升小技巧：</p>
              <ul>
                <li class="level-tips">每获得交易对象（买家）一次好评积1经验值，中评积0经验值，差评减1经验值</li>
              </ul>
            </div>
            <span
              style="vertical-align: super;color:#188AE2;cursor:pointer;margin-left:10px;"
              slot="reference"
              v-if="!tableData.store.selfShop"
            >提升等级</span>
          </el-popover>
        </div>
      </el-form-item>
      <el-form-item label="主题色：" >
        <el-color-picker v-model="postParams.storeSet.themeColor"></el-color-picker>
        <div class="gray">设置店铺首页导航条及店铺商品滑动效果主题色</div>
      </el-form-item>
      <el-form-item label="店铺Logo：" >
        <cms-picture-zone v-model="postParams.logoId" :data="postParams.storeLogoData">
          <cms-button v-color>上传</cms-button>
        </cms-picture-zone>
        <div class="gray">建议上传240*60尺寸的店铺Logo</div>
      </el-form-item>
      <el-form-item label="店铺头像：">
        <cms-picture-zone v-model="postParams.headImgId" :data="postParams.storeHeadImgData">
          <cms-button v-color>上传</cms-button>
        </cms-picture-zone>
        <div class="gray">建议上传64*64尺寸的正方形头像</div>
      </el-form-item>
      <el-form-item label="店铺条幅：">
        <cms-picture-zone
          v-model="postParams.storeSet.indeScrollPicId"
          :data="postParams.indeScrollPicData"
        >
          <cms-button v-color>上传</cms-button>
        </cms-picture-zone>
        <div class="gray">建议上传1920*120尺寸横条图</div>
      </el-form-item>
      <el-form-item label="店铺关键词：" prop>
        <el-input v-model="postParams.storeSet.keyWords" maxlength="30"></el-input>
        <div class="gray">设置店铺描述关键词，多个请使用逗号分隔</div>
      </el-form-item>
      <el-form-item label="店铺描述：" prop="description">
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4}"
          maxlength="100"
          v-model="postParams.storeSet.description"
        ></el-input>
        <div class="gray">设置店铺简要描述，仅限100字以内</div>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          size="mini"
          v-if="!updateOrSave"
          style="width:80px;"
          @click="update"
        >保存</el-button>
        <el-button
          type="primary"
          size="mini"
          v-if="updateOrSave"
          style="width:80px;"
          @click="update"
        >修改</el-button>
      </el-form-item>
    </el-form>
    <!-- 添加弹窗 -->
    <el-dialog title="操作提示" :visible="makeVisible" width="640px" :before-close="handleClose">
      <div class="dialog-form">
        <cms-prompt collapse="true">
          <li>系统会自动从账户余额中扣减，请确保账户余额内的资金充足！</li>
        </cms-prompt>
        <el-form :model="makeForm" ref="dialogForm">
          <el-form-item label="费用名称：" label-width="120px">
            <span>{{makeForm.name}}</span>
          </el-form-item>
          <el-form-item label="充值账户：" label-width="120px">
            <span>{{makeForm.account}}</span>
          </el-form-item>
          <el-form-item label="金额：" label-width="120px">
            <span>{{makeForm.price}}</span>
          </el-form-item>
          <el-form-item
            label="支付密码："
            label-width="120px"
            prop="pStr"
            :rules="
            [{ required: true, message: '请输入支付密码', trigger: 'blur' }]"
          >
            <el-input
              v-model="makeForm.pStr"
              type="password"
              :rows="4"
              placeholder="请输入支付密码"
              maxlength="16"
            ></el-input>
          </el-form-item>
          <div class="center">
            <cms-button @click="makeBond(makeForm)" style="margin-right:10px;">提交</cms-button>
            <cms-button @click="handleClose" type="info">取消</cms-button>
          </div>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getStoreSet,
  judgeStoreSet,
  updateStoreSet,
  getLevelList,
  storeSetOperationHint,
  storeSetSupplement
} from '@/api/shop/shopInfo.js'
export default {
  data() {
    return {
      tableData: {},
      loading: false,
      postParams: {}, // 修改或保存按钮的表单数据
      updateOrSave: '', // 修改或保存按钮的控制
      dialogVisible: false,
      makeForm: {},
      makeVisible: false,
      levelList: [] // 等级体系列表信息
    }
  },
  methods: {
    makeBond(data) {
      // 补交保证金
      this.$refs.dialogForm.validate(valid => {
        if (valid) {
          const params = {
            pStr: this.$cms.encrypt(data.pStr)
          }
          storeSetSupplement(params).then(res => {
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.handleClose()
              this.getTableData()
            } else {
              this.$errorMessage(res.message)
              this.handleClose()
            }
          })
        }
      })
    },
    make() {
      this.makeVisible = true
      storeSetOperationHint().then(res => {
        if (res.code === 200) {
          this.makeForm = {
            name: res.data.categoryName + '保证金差额',
            account: res.data.account,
            price: this.$cms.float(res.data.money),
            pStr: ''
          }
        }
      })
    },
    handleClose() {
      // 关闭弹窗
      this.makeVisible = false
    },
    getTableData() {
      this.loading = true
      getStoreSet().then(res => {
        if (res.code === 200) {
          this.tableData = res.data
          const params = {
            logoId: res.data.store.logoId,
            headImgId: res.data.store.headImgId,
            storeSet: {
              themeColor:
                res.data.themeColor !== '' ? res.data.themeColor : '#483E39',
              indeScrollPicId: res.data.indeScrollPicId,
              keyWords: res.data.keyWords,
              description: res.data.description
            },
            indeScrollPicData: res.data.resourcesSpaceData,
            storeLogoData: res.data.store.storeLogoResource,
            storeHeadImgData: res.data.store.storeHeadImgResource
          }

          this.postParams = Object.assign(this.postParams, params)
          this.loading = false
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    update() {
      // 保存店铺信息
      const params = {
        logoId: this.postParams.logoId,
        headImgId: this.postParams.headImgId,
        storeSet: {
          themeColor: this.postParams.storeSet.themeColor,
          indeScrollPicId: this.postParams.storeSet.indeScrollPicId,
          keyWords: this.postParams.storeSet.keyWords,
          description: this.postParams.storeSet.description
        }
      }
      updateStoreSet(params).then(res => {
        if (res.code === 200) {
          this.$successMessage('成功')
          this.getTableData()
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    judgeStoreSet() {
      // 修改或新增店铺按钮控制
      judgeStoreSet().then(res => {
        if (res.code === 200) {
          this.updateOrSave = res.fata
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    getLevelList() {
      // 获取店铺等级信息
      getLevelList().then(res => {
        if (res.code === 200) {
          this.levelList = res.data
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getTableData()
    this.judgeStoreSet()
    this.getLevelList() // 获取店铺等级信息
  }
}
</script>

<style scoped>
.table-wrap {
  position: relative;
}
.level-tips {
  list-style-type: disc;
  margin-left: 15px;
}
</style>




