<template>
  <div>
    <div class="safe-wrap" v-loading="loading" v-cloak>
      <div class="safe-item" style="border-bottom:1px solid #e0e0e0;">
        <div style="width:15%;margin-left:15px;">
          <i class="el-icon-success" style="font-size:30px;color:#71B247"></i>
        </div>
        <div style="width:20%;font-size:15px;font-weight:600;">
          结算账户
        </div>
        <div style="width:40%">
          <p><span class="safe-title">账户类型：</span><span class="safe-value">{{tableData.accountType|accountTypeFilter}}</span></p>
          <p><span class="safe-title">账&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;户：</span>
             <span class="safe-value">{{tableData.account}}</span>
          </p>
          <p><span class="safe-title">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名：</span>
             <span class="safe-value">{{tableData.accountName}}</span>
          </p>
        </div>
        <div  style="width:25%;text-align:center;">
          <el-button type="primary" size="mini" style="width:80px;" @click="$emit('event', 'Admin')">管理</el-button>
        </div>
      </div>
      <div class="safe-item">
        <div style="width:15%;margin-left:15px;">
          <i class="el-icon-warning" style="font-size:30px;color:#FF9926"></i>
        </div>
        <div  style="width:20%;font-size:15px;font-weight:600;">
          支付密码
        </div>
        <div style="width:40%">
          <p>支付密码可用于余额支付验证及账户余额提现等操作，确保您的账户资金安全，密码由6-16位字母和数字组成</p>
        </div>
        <div  style="width:25%;text-align:center;">
          <el-button type="primary" size="mini" style="width:80px;"  
          @click="passwordAdmin" v-if="!canSetPassword">设置密码
          </el-button>
          <el-button type="primary" size="mini" style="width:80px;"
          @click="passwordAdmin" v-if="canSetPassword" >更换密码
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSettleAccount, getAccountSet } from '@/api/shop/shopInfo.js'
export default {
  data() {
    return {
      tableData: {},
      loading: false,
      canSetPassword: ''// 设置或修改密码的显示隐藏
    }
  },
  filters: {
    accountTypeFilter(type) {
      const typeArr = ['', '支付宝 ', '微信', '银联']
      return typeArr[parseInt(type)]
    }
  },
  methods: {
    getTableData() { // 获取第一条支付信息
      this.loading = true
      getSettleAccount().then(res => {
        if (res.code === 200) {
          if (!res.data.selfShop) {
            this.tableData = res.data
          } else {
            this.tableData = {
              account: '',
              accountType: 0,
              accountName: ''
            }
          }
          this.loading = false
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    passwordAdmin() { // 切换到密码管理组件
      this.$emit('event', 'Password')
    },
    getAccountPassoword() { // 判断是否设置了商家端的支付密码
      getAccountSet().then(res => {
        if (res.code === 200) {
          this.canSetPassword = res.data
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getTableData()
    this.getAccountPassoword()
  }
}
</script>

<style scoped>
.safe-item{
  display: flex;
  align-items: center;
  padding: 20px 0;
}
.safe-title{
  display: inline-block;
  width: 70px;
  text-align: right;
}
.safe-value{
  display: inline-block;
  max-width: 120px;
}

</style>



